@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: 'Outfit', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p{
  font-size: 1.6rem;
}

h1{
  font-size:4rem;
}


.App{
  width:100%;
  position:relative;
  background-color: rgba(0,0,0,0.4);
  color: #fff;
}

@media (max-width: 767px) {
  .App .bottom {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 6rem auto
  }
}

@media (max-width: 376px) {
  .App .bottom {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 6rem auto
  }
}

.App:before {
  content: '';
  background: url('./imge/bg-img.jpg') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.App .search {
  text-align: center;
  padding: 1rem;
}

.App input {
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255,255,255, 0.8);
  background: transparent;
  color: #f8f8f8;
}


button {
  background-color: transparent;
  border: none;
  color: inherit;
  padding: 0.5em .5em;
  font-size: 1em;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: rgba(0, 0, 0, 0.1);
}


.container {
  max-width: 43.75rem;
  height: 43.75rem;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@media (max-width: 767px) {
  .container{
    height:38.75rem;
  }
}

.App .top{
  width:100%;
  margin:1rem auto;
}

.App .discription{
  position: relative;
  right: -94%;
  bottom: -29%;
  transform-origin: 0 0;
  transform: rotate(269deg);
}

.App .bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(0, 0, 0, 0.7);}


.bold{
  font-weight: 700;
} 

.footer {
  background-color: transparent;
  padding: 20px 0;
  text-align: center;
}

.footer p {
  margin: 0;
  font-size: 1rem;
  color: white;
}
